.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

// color
$colorYellow: #fdc344;
$colorIvory: #f5f0ec;
$colorDarkgrey: #3e3e3e;
$colorGrey: darkgray;
$colorLightgrey: #e7e2df;

//opacity
$opacity60: 0.6;
$opacity80: 0.8;

// box-shadow
$boxShadow: 0px 0px 40px #00000029;

// font-size
$font13: 13px;
$font16: 16px;
$font25: 25px;
$font100: 100px;
$font150: 150px;

// margin
$margin100: 100px;
$margin50: 50px;
$margin30: 30px;
$margin20: 20px;
$margin10: 10px;

// height
$height50: 50px;

// scale
$scale: scale(1.05);

// navbar
.navbar_wrap {
  .navbar {
    background-color: $colorYellow;
    color: $colorIvory;
    font-size: $font16;
    height: $height50;
    position: fixed;
    width: 100%;
    z-index: 5;
    a {
      text-decoration: none;
      color: $colorIvory;
    }
    ul {
      margin: 0 $margin30;
      padding: 0;
      width: 100%;
      li {
        display: inline;
      }
      .navMento {
        font-weight: bold;
        margin-right: $margin20;
        float: left;
      }
      .navList {
        margin-left: $margin20;
        right: 0;
        float: right;
      }
    }
  }
  .navbar_back {
    height: $height50;
    width: 100%;
  }
}

// home
.home {
  .header {
    background-color: $colorYellow;
    padding-top: $margin50;
    .main-title {
      position: relative;
      .mento {
        font-size: $font150;
        font-weight: bold;
        color: $colorDarkgrey;
        z-index: 1;
        position: relative;
      }
      .find {
        position: absolute;
        font-size: $font100;
        color: $colorIvory;
        opacity: $opacity60;
        position: absolute;
        top: 100px;
        width: 100%;
        text-align: center;
        z-index: 0;
      }
    }
    .search {
      padding: $margin100;
      font-size: $font16;
      form {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: $margin10;
        input {
          border-radius: 20px;
          width: 400px;
          height: 40px;
          border: none;
          padding: $margin20;
        }
        input:focus {
          outline: none;
        }
        input::placeholder {
          color: $colorGrey;
        }
        button {
          position: absolute;
          margin-left: 290px;
          width: 100px;
          height: 33px;
          border-radius: 20px;
          border: none;
          color: #fff;
          background: $colorDarkgrey;
        }
      }
    }
  }

  .category_icons {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: $margin100 0;
    .category_icons4 {
      display: flex;
      .category {
        text-align: center;
        margin: $margin30 $margin50;
        transition: 0.5s all ease;
        a {
          text-align: center;
          text-decoration: none;
          color: $colorDarkgrey;
          .icon {
            display: inline-block;
            width: 80px;
            height: 80px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: $colorIvory;
            box-shadow: $boxShadow;
            transition: 0.5s all ease;
            i {
              display: block;
              font-size: 2em;
              color: $colorDarkgrey;
            }
          }
          .icon:hover {
            // transform: $scale;
            width: 90px;
            height: 90px;
            font-size: 1.1em;
          }
          span {
            font-size: 13px;
            font-weight: bold;
            width: 100%;
            color: #3e3e3e;
          }
        }
      }
    }
  }

  .knowhow {
    background-color: $colorIvory;
    margin: 0 auto;
    padding: $margin100 0;
    .title {
      width: 900px;
      font-size: $font25;
      font-weight: bold;
      margin: 0 auto;
      text-align: left;
    }
    .btnWrap {
      position: relative;
      width: 950px;
      margin: 0 auto;
      .contentsWrap {
        width: 900px;
        margin: 0 auto;
        overflow: hidden;
        .contents {
          margin: 0 auto;
          width: 900px;
          display: flex;
          padding: 0 $margin10;
          .content {
            margin-top: $margin20;
            margin-right: $margin10;
            text-align: center;
            .content_img {
              display: block;
              margin-bottom: $margin10;
              border-radius: 20px;
              width: 280px;
              height: 200px;
              transition: 0.5s all ease;
              box-shadow: 0px 0px 20px #00000029;
            }
            .content_img:hover {
              transform: $scale;
            }
            .content_title {
              font-weight: bold;
              font-size: $font16;
            }
            .content_name {
              font-size: $font16;
            }
            span {
              display: block;
            }
            a {
              text-decoration: none;
              color: inherit;
            }
            a:hover {
              text-decoration: none;
              color: inherit;
            }
          }
        }
      }
      button {
        position: absolute;
        top: 40%;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        background: #ffffff;
        box-shadow: 0px 0px 40px #00000029;
      }
      button.before {
        @extend button;
        left: 10px;
      }
      button.next {
        @extend button;
        right: 0;
      }
    }
  }

  .magazine {
    @extend .knowhow;
    padding-top: 0;
  }

  .map {
    padding: $margin100 0;
    background-color: $colorLightgrey;
    font-size: $font16;
    text-align: left;
    .cities {
      width: 900px;
      margin: 0 auto;
      .title {
        display: block;
        font-size: $font25;
        font-weight: bold;
        margin-bottom: $margin30;
      }
      .text {
        font-weight: bold;
      }
      .text i {
        margin-right: $margin10;
      }
      ul.city {
        padding: 0;
        margin: $margin50 0;
        a {
          text-decoration: none;
          color: inherit;
          li {
            list-style: none;
            display: inline-block;
            margin-right: $margin100;
            margin-bottom: $margin20;
            transition: 0.5s all ease;
            backface-visibility: hidden;
          }
          li:hover {
            backface-visibility: hidden;
            // transform: perspective(90px) translateZ(0) $scale;
            font-size: 1.05em;
            font-weight: bold;
          }
        }
      }
    }
  }
}

// signin
.signin {
  .background {
    width: 100%;
    height: 900px;
    background-color: $colorLightgrey;
    display: flex;
    .frame {
      display: flex;
      height: 800px;
      width: 1000px;
      margin: auto;
      .signIn {
        background-color: #ffffff;
        width: 550px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-radius: 20px 0 0 20px;
        .title {
          font-size: $font25;
          width: 100%;
          text-align: center;
        }
        form {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          input:focus {
            outline: none;
          }
          input::placeholder {
            color: $colorGrey;
          }
          .error {
            display: block;
            color: red;
            font-size: $font13;
            margin-top: 5px;
            float: left;
          }
          .text {
            position: relative;
            width: 400px;
            label {
              float: left;
              margin-bottom: 5px;
              margin-top: $margin10;
            }
            input {
              border: 1px solid $colorDarkgrey;
              box-shadow: $boxShadow;
              border-radius: 20px;
              width: 100%;
              height: 40px;
              font-size: $font16;
              padding: $margin20;
            }
          }
          .find-account {
            display: block;
            color: $colorDarkgrey;
            margin-top: $margin10;
            font-size: $font16;
            text-align: left;
            width: 100%;
          }
          .btn input {
            border: none;
            border-radius: 20px;
            width: 400px;
            height: 40px;
            font-size: $font16;
            background: $colorYellow;
            box-shadow: $boxShadow;
          }
          .login-type {
            display: flex;
            flex-direction: column;
            width: 350px;
            margin-bottom: $margin10;
            .btn {
              margin-bottom: $margin10;
              border-radius: 20px;
            }
            .btn3 {
              background-color: #00c7ae;
              color: white;
              margin-top: $margin30;
            }
            .btn4 {
              background-color: #fee500;
              color: black;
            }
            .btn5 {
              background-color: #1876f1;
              color: white;
            }
          }
        }
      }
      .signUp {
        background-color: $colorDarkgrey;
        box-shadow: inset 0px 0px 5px $colorLightgrey;
        width: 450px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-radius: 0 20px 20px 0;
        .title {
          text-align: center;
          span {
            display: block;
          }
          .mento {
            color: #ffc245;
            font-size: 50px;
            font-weight: bold;
          }
        }
        .find {
          color: #e7e2df;
          font-size: 35px;
          font-weight: bold;
        }
        .btn {
          width: 100%;
          margin-top: 100px;
          button {
            border: none;
            border-radius: 40px;
            width: 200px;
            height: 40px;
            font-size: 15px;
            background: #fff;
          }
        }
      }
    }
  }
}

