// knowhowpg
.wrap {
  width: 85%;
  margin: $margin50 auto;
  border: 1px solid $colorLightgrey;
  border-radius: 20px;
  box-shadow: $boxShadow;
  header {
    margin: $margin50 auto;
    hr {
      width: 80%;
      margin: $margin20 auto;
    }
    img {
      margin: $margin50 auto;
      width: 500px;
      height: 350px;
      border-radius: 20px;
      box-shadow: $boxShadow;
    }
  }
  .writing {
    margin: $margin50 $margin100;
  }
  .comment-content {
    background-color: $colorIvory;
    border-radius: 0 0 20px 20px;
    padding: $margin50;
    .write {
      width: 100%;
      margin: $margin20 auto;
      border: 1px solid $colorLightgrey;
      border-radius: 20px;
      box-shadow: $boxShadow;
      textarea {
        width: 95%;
        height: 100px;
        padding: $margin20;
        margin-top: $margin20;
        box-sizing: border-box;
        border: solid 1px $colorLightgrey;
        border-radius: 20px;
        resize: none;
      }
      button {
        border: none;
        border-radius: 20px;
        width: 80px;
        height: 30px;
        font-size: $font13;
        background: $colorGrey;
        box-shadow: $boxShadow;
        margin: $margin10 auto;
        color: white;
        font-weight: bold;
      }
    }
    .comment {
      width: 100%;
      margin: $margin10 auto;
      padding: $margin10;
      border: 1px solid $colorLightgrey;
      border-radius: 20px;
      box-shadow: $boxShadow;
      text-align: left;
      span {
        font-weight: bold;
      }
      p {
        margin: 0;
        margin-top: $margin10;
      }
    }
  }
}

