        .content-body {
          .content-title {
            font-weight: bold;
            font-size: $font16;
          }
          margin-top: $margin10;
        }
      }
    }
  }
}

// mentoDetail
.mento-detail {
  width: 85%;
  margin: $margin100 auto;
  hr {
    width: 85%;
    margin: $margin30 auto;
  }
  .profile {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .img {
      width: 300px;
      img {
        width: 100%;
        border-radius: 20px;
      }
    }
    .card {
      width: 400px;
      border-radius: 20px;
      box-shadow: $boxShadow;
      padding-top: $margin20;
      padding-bottom: $margin20;
      .namediv {
        text-align: left;
        .name {
          font-weight: bold;
          font-size: $font25;
          margin-left: $margin30;
          margin-right: $margin20;
        }
        .feature {
          margin-right: $margin10;
        }
      }
      .ment {
        text-align: left;
        margin-left: $margin30;
      }
      hr {
        width: 85%;
        margin: $margin10 auto;
      }
      .feature {
        width: 60px;
      }
      .number {
        margin-top: $margin10;
        font-size: $font25;
        display: flex;
        justify-content: space-around;
      }
      .button {
        button {
          margin: $margin10;
          border-radius: 20px;
          font-size: $font16;
        }
      }
    }
  }
  .container {
    margin: $margin100 auto;
    .title {
      font-size: $font25;
      font-weight: bold;
    }
    .box {
      border: 1px solid $colorLightgrey;
      border-radius: 20px;
      box-shadow: $boxShadow;
      margin-top: $margin30;
      .percent {
        margin: $margin30 auto;
        .bar {
          background-color: $colorLightgrey;
          margin: 0 auto;
          width: 85%;
          height: 18px;
          border-radius: 20px;
          margin: $margin20 auto;
          .fill {
            background-color: $colorDarkgrey;
            width: 64%;
            height: 18px;
            color: white;
            font-weight: bold;
            font-size: $font13;
            border-radius: 20px;
          }
        }
      }
      .question {
        font-size: $font25;
      }
      .reason {
        border: 1px solid $colorLightgrey;
        border-radius: 20px;
        margin: $margin30 auto;
        width: 85%;
        ul {
          list-style: none;
          text-align: left;
          li {
            margin: $margin20 auto;
            input {
              margin-right: $margin10;
            }
          }
        }
        button {
          border: none;
          border-radius: 20px;
          width: 400px;
          height: 40px;
          font-size: $font16;
          background: $colorYellow;
          box-shadow: $boxShadow;
          margin: $margin20 auto;
        }
      }
    }
  }
  .container2 {
    margin: $margin100 auto;
    h2 {
      font-size: $font25;
      font-weight: bold;
    }
    .faq {
      width: 85%;
      margin: $margin30 auto;
    }
  }
}

