          margin-left: $margin20;
          span {
            display: block;
          }
          .title {
            font-size: $font25;
          }
          .sentence {
            margin: $margin10 auto;
          }
          .grade {
            span {
              display: inline;
              margin-right: $margin20;
            }
          }
        }
      }
    }
    .btn {
      width: 100%;
      display: flex;
      justify-content: right;
      .write {
        margin-top: $margin20;
        border: none;
        border-radius: 20px;
        width: 150px;
        height: 40px;
        font-size: $font16;
        background: $colorYellow;
        box-shadow: $boxShadow;
      }
    }
  }
}

// group-upload
.group-upload {
  margin: $margin50;
  box-shadow: $boxShadow;
  border-radius: 20px;
  form {
    padding: $margin50;
    hr {
      margin: $margin20;
      color: $colorGrey;
    }
    input {
      width: 100%;
      border: none;
      padding: $margin10;
    }
    input:focus {
      outline: none;
    }
    textarea {
      width: 100%;
      border: none;
      resize: none;
      padding: $margin10;
    }
    textarea:focus {
      outline: none;
    }
    select {
      border: none;
      width: 200px;
      text-align: center;
    }
    select:focus {
      outline: none;
    }
    option {
      border: none;
    }
    .deadline {
      width: 30%;
      margin-left: $margin20;
    }
    .btn {
      width: 100%;
      display: flex;
      justify-content: right;
      button {
        margin-top: $margin20;
        border: none;
        border-radius: 20px;
        width: 150px;
        height: 40px;
        font-size: $font16;
        background: $colorYellow;
        box-shadow: $boxShadow;
      }
    }
  }
}

.tabContent {
  text-align: left;
  border-radius: 20px;
  margin-bottom: $margin50;
  border: 1px solid $colorLightgrey;
  ul {
    margin: 0;
    padding: $margin20;
    width: 100%;
    li {
      background-color: white;
      list-style: none;
      display: inline-block;
      border: 1px solid $colorLightgrey;
      margin-bottom: $margin10;
      margin-right: $margin20;
      padding: $margin10 $margin20;
      border-radius: 20px;
      cursor: pointer;
    }
    li:hover {
      box-shadow: 0px 0px 10px #00000029;
    }
  }
  button {
    border: none;
    border-radius: 20px;
    width: 80px;
    height: 40px;
    font-size: $font16;
    background: $colorGrey;
    margin: $margin20;
    margin-top: 0;
  }
  button:hover {
    box-shadow: 0px 0px 20px #00000029;
  }
}

// groupdetail
.group-detail {
  section {
    width: 85%;
    margin: $margin50 auto;
    border: 1px solid $colorLightgrey;
    border-radius: 20px;
    box-shadow: $boxShadow;
    text-align: left;
    .content {
      padding: $margin30 $margin50;
      display: flex;
      align-items: center;
      img {
        width: 150px;
        border-radius: 20px;
      }
      .content-body {
        margin-left: $margin20;
        span {
          display: block;
        }
        .title {
          font-size: $font25;
        }
        .sentence {
          margin: $margin10 auto;
        }
        .grade {
          span {
            display: inline;
            margin-right: $margin20;
          }
        }
      }
    }
    hr {
      width: 85%;
      margin: 0 auto;
    }
    .writer {
      padding: $margin30 $margin50;
      span {
        font-weight: bold;
      }
      p {
        margin-top: $margin10;
      }
    }
    .info {
      background-color: $colorIvory;
      padding: $margin10 $margin50;
      .block {
        margin: $margin50 auto;
        h4 {
          font-size: $font25;
          margin-bottom: $margin20;
        }
        span {
          display: block;
          margin-bottom: $margin10;
        }
        .call {
          span {
            display: inline;
          }
        }
        img {
          width: 200px;
          height: 150px;
          border-radius: 20px;
          margin-right: $margin10;
        }
        p {
          padding-top: $margin10;
        }
      }
    }
    .review {
      padding: $margin10 $margin50;
      margin: $margin50 auto;
      .title {
        font-size: $font25;
      }
      .star {
        margin: $margin10 0;
      }
      hr {
        margin: $margin30;
      }
      .review-content {
        .name {
          font-weight: bold;
        }
        p {
          margin-top: $margin10;
          margin-bottom: $margin30;
        }
      }
    }
  }
  .request {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: $colorYellow;
    padding: $margin20 0;
    z-index: 1;
    cursor: pointer;
    .req {
      color: #fff;
      font-weight: bold;
    }
  }
}

// footer
footer {
  background-color: $colorDarkgrey;
  color: $colorIvory;
  font-size: $font13;
  padding: $margin100 0;
  a {
    text-decoration: none;
    color: inherit;
  }
  a:hover {
    text-decoration: none;
    color: inherit;
  }
  .link {
    text-align: center;
    span {
      font-weight: bold;
      margin-right: $margin30;
    }
  }
  .sentences {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .sentence {
    text-align: center;
    margin-top: $margin30;
    width: 800px;
  }
  .snsBtns {
    display: flex;
    justify-content: center;
    margin-top: $margin30;
  }
  .sns {
    background-color: $colorIvory;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-right: $margin20;
    a {
      color: $colorDarkgrey;
    }
    a:hover {
      color: $colorDarkgrey;
    }
  }
  i {
    font-size: $font16;
  }
}
